@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
  }

  input,
  textarea {
    @apply focus:outline-none;
  }

  .global-style {
    @apply bg-texture bg-secondary dark:bg-bgLightMode dark:bg-none font-primary text-white transition-all duration-300;
  }

  .section {
    @apply py-24 lg:min-h-screen flex items-center;
  }

  h1 {
    @apply text-[27px] font-bold;
  }
  h2 {
    @apply font-secondary text-primary my-6 text-center text-[24px] font-semibold dark:text-secondary;
  }

  h3 {
    @apply text-[19px] font-semibold bg-secondaryVariant text-center text-lg rounded-lg py-1;
  }

  h4 {
    @apply text-center mb-3;
  }

  .bg-skill-stack {
    @apply bg-gradient-to-b from-primaryVariant to-secondary;
  }
  .bg-portfolio-cards {
    @apply bg-gradient-to-b from-primaryVariant to-secondary;
  }
  .bg-portfolio-text {
    @apply bg-gradient-to-t from-primaryVariant to-secondaryVariant;
  }
  .bg-header {
    @apply bg-gradient-to-b from-primary to-secondary;
  }
  .bg-light-header {
    @apply bg-gradient-to-t from-bgLightMode to-primary;
  }

  .bg-nav {
    @apply bg-gradient-to-r from-primary to-light;
  }
  .bg-light-nav {
    @apply bg-gradient-to-l from-bgLightMode to-primary;
  }
  .contact-btn {
    @apply dark:bg-primary dark:hover:bg-transparent bg-secondaryVariant dark:text-secondary p-5 rounded-3xl text-center
     border border-transparent transition-all duration-300
      hover:bg-transparent hover:border-primaryVariant;
  }
}

.bg-light-switch {
  background: #242424;
  box-shadow: inset 0px 5px 15px rgba(0, 0, 0, 0.4),
    inset 0px -5px 15px rgba(255, 255, 255, 0.4);
  cursor: pointer;
}
.bg-dark-switch {
  background: #242424;
  box-shadow: inset 0px 5px 15px rgba(0, 0, 0, 0.4),
    inset 0px -5px 15px rgba(255, 255, 255, 0.4);
  cursor: pointer;
}

.bg-skill-cards {
  background: linear-gradient(rgba(255, 255, 255, 0.6), #2c2c6c);
}

.bx-shad {
  box-shadow: 1px 1px 5px #4db5ff;
}

.box {
  border: 7px solid #4db5ff;
  border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  background-image: linear-gradient(#4db5ff, #1f1f38);
  animation: morph 8s ease-in-out infinite;
}

/* ---------------------Bouton 1 2 and 3-----------------  */
.btn {
  color: #4db5ff;
  background: #1f1f38;
  border: solid #4db5ff 1px;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  position: relative;
  z-index: 1;
  overflow: hidden;
  display: block;
  width: fit-content;
  font-size: 12px;
  margin: 5px;
}

.btn:hover {
  color: #1f1f38;
}

.btn::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 100%;
  background: #4db5ff;
  border-radius: 5px;
  z-index: -1;
  transition: transform 0.8s;
  transform-origin: 0 0;
  transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
  transform: scaleX(0);
}

.btn:hover::before {
  transform: scaleX(1);
}

