/* --------------------- Animation Hero --------------------------------- */

@keyframes morph {
  0% {
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  }
  50% {
    border-radius: 60% 40% 30% 70% / 50% 60% 30% 60%;
  }
  100% {
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  }
}

/*----------------- Code pour le Switch Light------------------  */

.dark_mode {
}

.dark_mode_label {
  width: 50px;
  height: 26px;
  position: relative;
  display: block;
  background: #242424;
  border-radius: 200px;
  box-shadow: inset 0px 5px 15px rgba(0, 0, 0, 0.4),
    inset 0px -5px 15px rgba(255, 255, 255, 0.4);
  cursor: pointer;
  transition: 0.3s;
}
.dark_mode_label:after {
  content: "";
  width: 23px;
  height: 23px;
  position: absolute;
  top: 1px;
  left: 0px;
  background: #1f1f38;
  border-radius: 180px;
  transition: 0.3s;
}
.dark_mode_input {
  width: 0;
  height: 0;
  visibility: hidden;
}
.dark_mode_input:checked + .dark_mode_label {
  background: #ebebeb;
}
.dark_mode_input:checked + .dark_mode_label:after {
  left: 50px;
  transform: translateX(-100%);
  background: #4db5ff;
}
.dark_mode_label:active:after {
  width: 30px;
}

.dark_mode_label svg {
  position: absolute;
  width: 20px;
  top: 4px;
  z-index: 100;
}
.dark_mode_label svg.sun {
  left: 29px;
  fill: #7e7e7e;
  transition: 0.3s;
}
.dark_mode_label svg.moon {
  left: 2px;
  fill: #fff;
  transition: 0.3s;
}
.dark_mode_input:checked + .dark_mode_label svg.sun {
  fill: #fff;
}
.dark_mode_input:checked + .dark_mode_label svg.moon {
  fill: #7e7e7e;
}
